import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import clearPng from '../assets/clear_16x9.png';
import pricingData from '../pricingData';

class Pricing extends Component {
  render () {
    const dataToJsx = datas => {
      const jsx = datas.map((data, i) => (
        <div className={`${i === datas.length - 1 ? 'col-md-12': 'col-md-6'} mb-5`} key={i}>
          <div className="card">
            <div className="card-header bg-dark text-white" style={{
              fontWeight: 'bold'
            }}>
              <span className="text-gold" style={{fontSize: '1.25em'}}>{data.title.slice(0, 1)}</span>{data.title.slice(1)}
            </div>
            <img src={clearPng} className="card-img-top" alt={data.title} style={{
              backgroundImage: `url(${data.image})`,
              backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center center',
              backgroundSize: '100%'
            }} />
            <div className="card-body">
              <div className="card-text text-black">
                {data.description}</div>
            </div>
          </div>
        </div>
      ));

      return jsx;
    };

    return (
      <Router>
        <section className="content-section text-white bg-dark" id="pricing">
          <div className="container">
            <div className="content-section-heading text-center">
              <h3 className="text-secondary mb-0">Pricing</h3>
              <h2 className="mb-5">Please select your plan.</h2>
            </div>

            <div className="row">
              {dataToJsx(pricingData)}
            </div>

            <h6>■注意事項</h6>
            <ul>
              <li>すべてのプランにおいて別途撮影場所までの交通費が発生いたします。</li>
            </ul>

          </div>
        </section>
      </Router>
    );
  };
}

export default Pricing;
